import cTitle from 'components/title';
import { mapState, mapMutations } from 'vuex';
import { Indicator, Toast } from 'mint-ui';
//import cOrder from 'components/order';

const TAG_ACTION_BUY = "-2";//直接购买
const TAG_ACTION_CART = "-1";//1购物车结算
var _root_tag = "";//0 直接购买 1购物车结算
var _goodsId = "";
var _optionsId = "";
var _total = 1;

var currentAddId = "";
var currentAddress = {};

var _cart_ids = [];
export default {
    data() {
        return {
            goodsInfo: {},
            order: {},
            dispatch: {},
            realname: "",//收件人
            mobile: "",
            address: "",
            goods: [],
            goods_price: 0,
            dispatch_price: 0,
            price: 0,
            popupSpecs: false,
            addressData: [],
        }
    },


    activated() {
       
        console.log("activated");
        this.goodsInfo = {};
        this.order = {};
        this.dispatch = {};
        this.addressData = [];

        currentAddId="";
        //接收参数
        _root_tag = this.$route.params.tag;

        console.log("_root_tag",_root_tag);
        if(this.fun.isTextEmpty(_root_tag))
        {
            //为空跳回
             this.$router.push({ name: "home", params: {} });
        }


        if (_root_tag == TAG_ACTION_BUY) {
            _goodsId = this.$route.params.goodsId;
            _optionsId = this.$route.params.optionsId;
            _total = this.$route.params.total;

            this.getDataActionBuy();//直接购买
        } else if (_root_tag == TAG_ACTION_CART) {
            _cart_ids = this.$route.params.cart_ids;
            this.getDataActionCart();//购物车结算
        }

        this.slider();

    },

    mounted() {

    },
    methods: {
        slider() {

        },

        //直接购买
        getDataActionBuy() {
            let that = this;
            let json = { goods_id: _goodsId, total: _total, option_id: _optionsId };
            console.log(json);
            $http.get('order.goods-buy', json, "生成中...").then(function (response) {
                console.log(response.data)
                if (response.result == 1) {
                    that.goodsInfo = response.data;
                    //that.order = response.data.order;
                    //that.dispatch = response.data.dispatch;
                    that.setViewData(response.data);//设置界面
                    that.setAddressViewData(response.data.dispatch.default_member_address);//设置地址界面
                } else {

                }
            }, function (response) {
                console.log(response);
            });
        },

        //购物车结算
        getDataActionCart() {
            let that = this;
            let json = { cart_ids: _cart_ids };
            console.log(json);
            $http.get('order.cart-buy', json, "生成中...").then(function (response) {
                console.log(response.data)
                if (response.result == 1) {
                    that.goodsInfo = response.data;
                    //that.order = response.data.order;
                    //that.dispatch = response.data.dispatch;
                    that.setViewData(response.data);//设置界面
                    that.setAddressViewData(response.data.dispatch.default_member_address);//设置地址界面
                } else {

                }
            }, function (response) {
                console.log(response)
            });
        },

        //设置界面
        setViewData(data) {

            this.order_data = data.order_data;

            this.goods_price = data.total_price;//设置商品总价格
            this.dispatch_price = data.total_dispatch_price;//设置总运费价格
            this.price = data.total_goods_price;//设置总价格（合计）
            // this.goods = data.order.order_goods;
            // this.goods_price = data.order.goods_price;
            // this.dispatch_price = data.order.dispatch_price;
            // this.price = data.order.price;
        },

        //设置地址界面
        setAddressViewData(model) {
            //设置地址id
            currentAddId = model.id;
            currentAddress = model;

            this.realname = model.username;
            this.mobile = model.mobile;
            this.address = model.province + " " + model.city + " " + model.district + " " + model.address;
        },

        //显示地址pop
        showAddress() {
            //获取收货地址
            this.getAddress();
        },

        //获取收货地址
        getAddress() {
            this.addressData = [];
            let that = this;
            let json = {};
            $http.get('member.member-address.index', json, "获取中...").then(function (response) {
                console.log(response.data)
                if (response.result == 1) {
                    that.popupSpecs = true;
                    that.addressData = response.data;
                } else {

                }
            }, function (response) {
                console.log(response);
            });
        },

        //选择地址
        selectAddress(item) {
            console.log(item);
            this.setAddressViewData(item);//更新界面 & ID
            //关闭 地址栏
            this.popupSpecs = false;
        },

        //跳转到新增地址
        addAddress()
        {
            //关闭 地址栏
            this.popupSpecs = false;
             this.$router.push('/member/appendAddress');
        },

        //提交订单
        submit() {

            if(this.fun.isTextEmpty(currentAddId))
            {
                Toast("请选择收货地址");
                return;
            }

            let that = this;
            let json = this.assembleJson();
            $http.get('order.create', json, "获取中...").then(function (response) {
                console.log(response.data)
                if (response.result == 1) {

                    that.$router.push({ name: "orderlist", params: { status: "1" } });
                    //that.$router.push({ name: "orderpay", params: { order_id: response.data.order_id } });
                } else {
                    Toast(response.msg);
                }
            }, function (response) {
                console.log(response);
            });
        },

        //组装json数据
        assembleJson() {
            let json = {};
            if (_root_tag == TAG_ACTION_BUY) {
                json = { address: JSON.stringify(currentAddress), goods: JSON.stringify(this.assembleGoods()) };
            } else if (_root_tag == TAG_ACTION_CART) {
                json = { address: JSON.stringify(currentAddress), goods: JSON.stringify(this.assembleGoods()), cart_ids: JSON.stringify(_cart_ids) };
            }
            return json;
        },
        //组装商品信息
        assembleGoods() {
            let newGoods = [];

            for (let j = 0; j < this.goodsInfo.order_data.length; j++) {
                for (let i = 0; i < this.goodsInfo.order_data[j].order.order_goods.length; i++) {
                    let model = new Object();
                    model.goods_id = this.goodsInfo.order_data[j].order.order_goods[i].goods_id;
                    model.total = this.goodsInfo.order_data[j].order.order_goods[i].total;
                    model.option = this.goodsInfo.order_data[j].order.order_goods[i].goods_option_id;
                    newGoods.push(model);
                }
            }


            return newGoods;
        }
    },
    components: { cTitle }
}


